import PropTypes from "prop-types";
import * as Yup from "yup";
import { useEffect, useMemo } from "react";
import { useSnackbar } from "notistack";
import { useNavigate } from "react-router-dom";
// form
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
// @mui
import { LoadingButton } from "@mui/lab";
import { Box, Card, Grid, Stack, Typography } from "@mui/material";
// utils
// routes
import { PATH_DASHBOARD } from "../../../routes/paths";
// components
import {
  FormProvider,
  RHFSelect,
  RHFTextField,
  RHFSwitch,
} from "../../../components/hook-form";
import { useDispatch, useSelector } from "../../../redux/store";
import {
  getTypeTransactions,
  getCurrencies,
} from "../../../redux/slices/generale";
import {
  addLimitTransactionGenerale,
  updateLimitTransactionGenerale,
  addLimitTransactionGeneraleSuccess,
  updateLimitTransactionGeneraleSuccess,
} from "../../../redux/slices/limittransaction";
import extractErrorMessage from "../../../utils/error";
import { useTranslation } from "react-i18next";
// ----------------------------------------------------------------------

LimitTransactionGeneraleNewEditForm.propTypes = {
  isEdit: PropTypes.bool,
  currentLimitTransactionGenerale: PropTypes.object,
};

export default function LimitTransactionGeneraleNewEditForm({
  isEdit,
  currentLimitTransactionGenerale,
}) {
  const navigate = useNavigate();

  const { t } = useTranslation();

  const { enqueueSnackbar } = useSnackbar();

  const NewLimitTransactionGeneraleSchema = Yup.object().shape({
    type_transaction: Yup.string().required(
      t("field.required", {
        obj: t("field.type_transaction"),
      })
    ),
    currency: Yup.string().required(
      t("field.required", {
        obj: t("field.currency"),
      })
    ),
    daily_limit: Yup.string().required(
      t("field.required", {
        obj: t("field.daily_limit"),
      })
    ),
    daily_amount: Yup.number().required(
      t("field.required", {
        obj: t("field.daily_amount"),
      })
    ),
    weekly_limit: Yup.string().required(
      t("field.required", {
        obj: t("field. weekly_limit"),
      })
    ),
    weekly_amount: Yup.number().required(
      t("field.required", {
        obj: t("field. weekly_amount"),
      })
    ),
    monthly_limit: Yup.string().required(
      t("field.required", {
        obj: t("field.monthly_limit"),
      })
    ),
    monthly_amount: Yup.number().required(
      t("field.required", {
        obj: t("field.monthly_amount"),
      })
    ),
  });

  const defaultValues = useMemo(
    () => ({
      type_transaction: currentLimitTransactionGenerale?.type_transaction || "",
      currency: currentLimitTransactionGenerale?.currency || "",
      daily_limit: currentLimitTransactionGenerale?.daily_limit || 0,
      daily_amount: currentLimitTransactionGenerale?.daily_amount || 0,
      weekly_limit: currentLimitTransactionGenerale?.weekly_limit || 0,
      weekly_amount: currentLimitTransactionGenerale?.weekly_amount || 0,
      monthly_limit: currentLimitTransactionGenerale?.monthly_limit || 0,
      monthly_amount: currentLimitTransactionGenerale?.monthly_amount || 0,
      is_activate: currentLimitTransactionGenerale?.is_activate || false,
      keyId: currentLimitTransactionGenerale?.id || -1,
    }),
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [currentLimitTransactionGenerale]
  );

  const methods = useForm({
    resolver: yupResolver(NewLimitTransactionGeneraleSchema),
    defaultValues,
  });

  const dispatch = useDispatch();

  const {
    reset,
    watch,
    control,
    setValue,
    handleSubmit,
    formState: { isSubmitting },
  } = methods;

  const values = watch();

  useEffect(() => {
    if (isEdit && currentLimitTransactionGenerale) {
      reset(defaultValues);
    }
    if (!isEdit) {
      reset(defaultValues);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEdit, currentLimitTransactionGenerale]);

  const onSubmit = async () => {
    try {
      if (isEdit) {
        const results = await updateLimitTransactionGenerale(values);
        dispatch(updateLimitTransactionGeneraleSuccess(results));
      } else {
        const results = await addLimitTransactionGenerale(values);
        dispatch(addLimitTransactionGeneraleSuccess(results));
      }
      reset();
      enqueueSnackbar(!isEdit ? t("success.CREATE") : t("success.UPDATED"));
      navigate(PATH_DASHBOARD.security_and_limit.limit_transaction_generale);
    } catch (error) {
      console.error(error);

      const errorMessage = extractErrorMessage(error);

      enqueueSnackbar(t(`error.${errorMessage}`), {
        variant: "error",
      });
    }
  };

  const { typetransactions, currencies } = useSelector(
    (state) => state.generale
  );

  useEffect(() => {
    dispatch(getTypeTransactions());
    dispatch(getCurrencies());
  }, [dispatch]);

  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={8}>
          <Card sx={{ p: 3 }}>
            <Box
              sx={{
                display: "grid",
                columnGap: 2,
                rowGap: 3,
                gridTemplateColumns: {
                  xs: "repeat(1, 1fr)",
                  sm: "repeat(2, 1fr)",
                },
              }}
            >
              <RHFSelect
                name="type_transaction"
                label={t("field.type_transaction")}
                placeholder={t("field.type_transaction")}
                disabled={isEdit}
              >
                <option value="" />
                {typetransactions?.map((option) => (
                  <option key={option} value={option}>
                    {t(`type_transaction.${option}`)}
                  </option>
                ))}
              </RHFSelect>
              <RHFSelect
                name="currency"
                label={t("field.currency")}
                placeholder={t("field.currency")}
                disabled={isEdit}
              >
                <option value="" />
                {currencies?.map((option) => (
                  <option key={option} value={option}>
                    {option}
                  </option>
                ))}
              </RHFSelect>

              <RHFTextField name="daily_limit" label={t("field.daily_limit")} />
              <RHFTextField
                name="daily_amount"
                label={t("field.daily_amount")}
              />
              <RHFTextField
                name="weekly_limit"
                label={t("field.weekly_limit")}
              />
              <RHFTextField
                name="weekly_amount"
                label={t("field.weekly_amount")}
              />
              <RHFTextField
                name="monthly_limit"
                label={t("field.monthly_limit")}
              />
              <RHFTextField
                name="monthly_amount"
                label={t("field.monthly_amount")}
              />
              <RHFSwitch
                name="is_activate"
                labelPlacement="start"
                label={
                  <>
                    <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                      Activated
                    </Typography>
                    <Typography
                      variant="body2"
                      sx={{ color: "text.secondary" }}
                    >
                      Enabling this will automatically make this limit on
                    </Typography>
                  </>
                }
                sx={{ mx: 0, width: 1, justifyContent: "space-between" }}
              />
            </Box>

            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton
                type="submit"
                variant="contained"
                loading={isSubmitting}
              >
                {!isEdit
                  ? t("params.create", {
                      obj: t(
                        "p_limit_transaction_generale.limit_transaction_generale"
                      ),
                    })
                  : t("params.save_change")}
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
