import { useEffect, useState } from "react";
import { useSnackbar } from "notistack";
import { useParams, useLocation, useNavigate } from "react-router-dom";
// @mui
import { Container } from "@mui/material";
// routes
import { PATH_DASHBOARD } from "../../routes/paths";
import { useDispatch } from "../../redux/store";
// hooks
import useSettings from "../../hooks/useSettings";
import {
  getLimitTransactionGenerale,
  getLimitTransactionGenerales,
} from "../../redux/slices/limittransaction";
// components
import Page from "../../components/Page";
import HeaderBreadcrumbs from "../../components/HeaderBreadcrumbs";
// sections
import LimitTransactionGeneraleNewEditForm from "../../sections/@dashboard/security-and-limit/LimitTransactionGeneraleNewEditForm";
import extractErrorMessage from "../../utils/error";
import { useTranslation } from "react-i18next";
// ----------------------------------------------------------------------

export default function LimitTransactionGeneraleCreate() {
  const { themeStretch } = useSettings();

  const { t } = useTranslation();

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();

  const { pathname } = useLocation();

  const { limitTransactionKey = "" } = useParams();

  const isEdit = pathname.includes("edit");

  const [currentLimitTransactionGenerale, setCurrentLimitTransactionGenerale] = useState(null);

  useEffect(() => {
    if (isEdit) {
      handleGetObject();
    }
  }, [limitTransactionKey]);

  const handleGetObject = async () => {
    try {
      const result = await getLimitTransactionGenerale(
        Number(limitTransactionKey)
      );
      setCurrentLimitTransactionGenerale(result);
      dispatch(getLimitTransactionGenerales());
    } catch (error) {
      console.error(error);

      const errorMessage = extractErrorMessage(error);

      enqueueSnackbar(errorMessage, {
        variant: "error",
      });
      navigate(PATH_DASHBOARD.security_and_limit.limit_transaction_generale);
    }
  };

  return (
    <Page
      title={`Security And Limit: ${
        !isEdit
          ? t("p_limit_transaction_generale.new_limit_transaction_generale")
          : t("p_limit_transaction_generale.edit_limit_transaction_generale")
      }`}
    >
      <Container maxWidth={themeStretch ? false : "lg"}>
        <HeaderBreadcrumbs
          heading={
            !isEdit
              ? t("p_limit_transaction_generale.new_limit_transaction_generale")
              : t(
                  "p_limit_transaction_generale.edit_limit_transaction_generale"
                )
          }
          links={[
            { name: t("params.dashboard"), href: PATH_DASHBOARD.root },
            {
              name: t("params.security_and_limit"),
              href: PATH_DASHBOARD.security_and_limit.root,
            },
            {
              name: !isEdit
                ? t(
                    "p_limit_transaction_generale.new_limit_transaction_generale"
                  )
                : `${t(
                    "p_limit_transaction_generale.limit_transaction_generale"
                  )} ${currentLimitTransactionGenerale?.type_transaction_name}`,
            },
          ]}
        />

        <LimitTransactionGeneraleNewEditForm
          isEdit={isEdit}
          currentLimitTransactionGenerale={currentLimitTransactionGenerale}
        />
      </Container>
    </Page>
  );
}
